.nb-m-page-header {
  position: relative;
  height: $header-height;
  line-height: $header-height;
  background-color: $color-primary;
  color: $color-primary-bg-text;
  .visible-t{
    display: block;
  }
  .visible-f{
    display: none;
  }
  .title, .return, .opts {
    font-size: 18px;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
  }
  .inner{
    height: $header-height;
  }
}