.nb-m-p-list {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .nb-m-p-list-bg{
    position: absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    width:100%;
    height:100%;
    z-index: 100;
    opacity: 0;
    transition: opacity 1s ease;
    &.show{
      opacity: 1;
    }
    .list-bg{
      margin-top: 20px;
      text-align: center;
    }
  }
  .nb-m-p-list-refresh {
    position: absolute;
    display: none;
    width: 100%;
    top: 0;
    text-align: center;
    z-index: 200;
    .tip {
      margin-left: 6px;
    }
  }
  .nb-m-scroll {
    position: absolute;
    -webkit-overflow-scrolling: touch;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 300;
  }
  .nb-m-p-list-list {
    position: absolute;
    width: 100%;
    top: 0;
    margin-bottom: 0;
  }
  .nb-m-p-list-more {
    position: absolute;
    display: none;
    width: 100%;
    bottom: 0;
    text-align: center;
    z-index: 200;
    .tip {
      margin-left: 6px;
    }
  }
  &[data-list-state=init] {
    .nb-m-p-list-refresh {

    }
  }
  &[data-list-state=releaseToRefresh] {
    .nb-m-p-list-refresh {
      display: block;
      .icon-refresh {
        transform: rotate(-180deg);
        transition: transform 0.5s ease-in-out;
      }
    }
  }
  &[data-list-state=pullDownToRefresh] {
    .nb-m-p-list-refresh {
      display: block;
      .icon-refresh {
        transform: rotate(0deg);
        transition: transform 0.5s ease-in-out;
      }
    }
  }
  &[data-list-state=refreshData] {
    .nb-m-p-list-refresh {
      display: block;
      .icon-refresh {
        animation: nb-m-ani-spin infinite 1s linear;
      }
    }
  }
  &[data-list-state=getMore] {
    .nb-m-p-list-more {
      display: block;
      .icon-more {
        animation: nb-m-ani-spin infinite 1s linear;
      }
    }
  }
  &[data-list-state=pullUpToMore] {
    .nb-m-p-list-more {
      display: block;
      .icon-more {
        transform: rotate(0deg);
        transition: transform 0.5s ease-in-out;
      }
    }
  }
  &[data-list-state=releaseToMore] {
    .nb-m-p-list-more {
      display: block;
      .icon-more {
        transform: rotate(-180deg);
        transition: transform 0.5s ease-in-out;
      }
    }
  }
}
