@keyframes nb-m-ani-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

.nb-m-ani-spin {
  animation-name: nb-m-ani-spin;
}

@keyframes nb-m-ani-fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.nb-m-ani-fadeIn {
  animation-name: nb-m-ani-fadeIn;
}

@keyframes nb-m-ani-fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.nb-m-ani-fadeOut {
  animation-name: nb-m-ani-fadeOut;
}

@keyframes nb-m-ani-slideInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.nb-m-ani-slideInLeft {
  animation-name: nb-m-ani-slideInLeft;
}

@keyframes nb-m-ani-slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.nb-m-ani-slideInRight {
  animation-name: nb-m-ani-slideInRight;
}

@keyframes nb-m-ani-slideOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(-100%, 0, 0);
  }
}

.nb-m-ani-slideOutLeft {
  animation-name: nb-m-ani-slideOutLeft;
}

@keyframes nb-m-ani-slideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(100%, 0, 0);
  }
}

.nb-m-ani-slideOutRight {
  animation-name: nb-m-ani-slideOutRight;
}