.nb-m-page {
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  &.fadeIn {
    display: block;
    z-index: 800;
    animation: nb-m-ani-fadeIn .6s .1s ease-out both;
  }
  &.slideOutLeft {
    display: block;
    z-index: 700;
    animation: nb-m-ani-slideOutLeft .5s .1s ease-out both;
  }
  &.slideInRight {
    display: block;
    z-index: 800;
    animation: nb-m-ani-slideInRight .5s .1s ease-out both;
  }
  &.slideOutRight {
    display: block;
    z-index: 700;
    animation: nb-m-ani-slideOutRight .5s .1s ease-out both;
  }
  &.slideInLeft {
    display: block;
    z-index: 800;
    animation: nb-m-ani-slideInLeft .5s .1s ease-out both;
  }
  &.header-visible-t {
    .nb-m-page-header {
      display: block;
    }
    .nb-m-page-content-wrapper {
      top: $header-height;
    }
  }
  &.header-visible-f {
    .nb-m-page-header {
      display: none;
    }
  }
  &[data-ready=false] {
    .nb-m-page-content{
      display: none;
    }
    .nb-m-page-content-mask{
      display: block;
    }
  }
  &[data-ready=true] {
    .nb-m-page-content{
      animation: nb-m-ani-fadeIn .6s .1s ease-out both;
    }
  }
}

.nb-m-page-content-wrapper, .nb-m-page-content, .nb-m-page-content-mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.nb-m-page-content-mask {
  display: none;
}